<template>
 
  <div v-if="id !='0'">

    <h1 class="mt-5">{{getClient.nombre}}</h1>
    <BuscarNumeroSeleccion/>
    <tableNumeroCliente/>
  </div>
  <div v-else>
    <h1>No ha sido escogido el numero</h1>
  </div>

  
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  props:{
    id:{
      type: String,
      required: true
    }
  },
  components:{
    BuscarNumeroSeleccion: defineAsyncComponent(()=> import('@/modules/rifa/components/numeros/buscarNumeroSeleccion.vue')),
    tableNumeroCliente : defineAsyncComponent(()=> import('@/modules/rifa/components/numeros/tablaNumeroCliente.vue'))
  },
  computed:{
    ...mapGetters('cliente',['getClient'])
  },
  methods:{
    ...mapActions('cliente',['getCliente']),
    ...mapMutations('cliente',['restaurarCliente']),
    ...mapActions('numeros',['getNumerosCliente']),  
  },
  
  created(){
    if(this.id !='0'){
      this.getCliente(this.id)
      this.getNumerosCliente(this.id)
    }
  },
  beforeUnmount(){
    this.restaurarCliente()
  }
  
}
</script>

